/* general styling */

html,
body {
  font-family: "Montserrat", sans-serif;
}

#navbar {
  height: 100%;
  width: 16em;
  position: fixed;
  z-index: 1;
  background-color: black;
  top: 0;
  left: 0;
  overflow-x: hidden;
  padding-top: 60px;
  transition: 0.5s;
}

#navbar header {
  padding: 8px 8px 8px 32px;
  text-decoration: none;
  font-size: 25px;
  color: whitesmoke;
  display: block;
  transition: 0.3s;
  margin: 30px;
}

#navbar a {
  padding: 8px 8px 8px 32px;
  text-decoration: none;
  font-size: 18px;
  color: whitesmoke;
  display: block;
  transition: 0.3s;
  margin: 30px;
}

#navbar a:hover {
  color: #7e6e6e;
}

#main-doc {
  margin-left: 18em;
  margin-top: 8em;
}

.main-section header {
  margin-top: 2em;
  margin-bottom: 2em;
  font-size: 1.23em;
}

code {
  background: #f7f7f7;
  color: #37474f;
  font: 400 100%/1 Roboto Mono, monospace;
  padding: 1px 4px;
}

/* bottom button styling */

.custom-btn-group .row {
  background: #f7f7f7;
}

/* mobile stylings */

@media (max-width: 768px) {
  #navbar {
    height: 300px;
    width: 100%;
    position: absolute;
    z-index: 1;
    background-color: black;
    top: 0;
    left: 0;
    overflow-x: hidden;
    padding-top: 0px;
    transition: 0.5s;
  }

  #navbar header {
    padding: 8px 8px 8px 32px;
    text-decoration: none;
    font-size: 18px;
    color: whitesmoke;
    display: block;
    transition: 0.3s;
    margin-top: 1em;
    margin-bottom: 1.3em;
    text-align: center;
  }

  #navbar a {
    padding: 8px 8px 8px 32px;
    text-decoration: none;
    font-size: 18px;
    color: whitesmoke;
    display: block;
    transition: 0.3s;
    margin: 0px;
  }

  #main-doc {
    margin-top: 350px;
    margin-left: 1em;
    margin-right: 1em;
  }
  .main-section p {
    font-weight: 1.2em;
  }

  .main-section header {
    font-size: 1.4em;
  }
  /* buttons */

  .btn.btn-primary {
    margin: 0.5em;
  }
}
